// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// i18n
import VueI18n from 'vue-i18n'
import Cookie from 'vue-cookie'

Vue.config.productionTip = false
Vue.use(VueI18n)

let i18n = new VueI18n({
  locale: 'cn', // 语言标识
  // this.$i18n.locale // 通过切换locale的值来实现语言切换
  messages: {
    cn: require('./lang/cn.json'), // 中文语言包
    en: require('./lang/en.json') // 英文语言包
  }
})

/**
 * 当url中没有语言信息时替换逻辑
 *
 * @param {*} _currentLang 传入语言信息
 * @param {*} toPath 当前url
 * @returns
 */
function replaceUrlRouter (_currentLang, toPath) {
  var _currentUrl = ''
  switch (_currentLang) {
    case 'zh-CN':
      _currentUrl = '/cn' + toPath
      break
    case 'zh-TW':
      _currentUrl = '/cn' + toPath
      break
    case 'zh-HK':
      _currentUrl = '/cn' + toPath
      break
    case 'cn':
      _currentUrl = '/cn' + toPath
      break
    case 'zh':
      _currentUrl = '/cn' + toPath
      break
    case 'en':
      _currentUrl = '/en' + toPath
      break
    case 'en-GB':
      _currentUrl = '/en' + toPath
      break
    case 'en-US':
      _currentUrl = '/en' + toPath
      break
    default:
      _currentUrl = '/en' + toPath
      break
  }
  return _currentUrl
}

router.beforeEach((to, from, next) => {
  var _currentLang
  // 0.没有cookie支持 按默认cn
  if (navigator.cookieEnabled === false) {
    // console.log("禁用cookie后:")
    if (to.fullPath.substr(0, 3) === '/cn') {
      _currentLang = 'cn'
    } else if (to.fullPath.substr(0, 3) === '/en') {
      _currentLang = 'en'
    } else {
      _currentLang = 'cn'
      router.replace(replaceUrlRouter(_currentLang, to.fullPath))
    }
    i18n.locale = _currentLang
  } else {
    // 1. 判断url是否带有语言信息
    if (to.fullPath.indexOf('/cn') < 0 && to.fullPath.indexOf('/en') < 0) {
      // console.log("1.url中没语言标识进入___if___:" + to.fullPath)
      // 2. 判断有没有已经存在cookie
      if (Cookie.get('jdcloud_sitelang')) {
        // 3-1. 判断cookie是否有写入
        _currentLang = Cookie.get('jdcloud_sitelang')
      } else {
        // 3-2. 将浏览器默认语言写入
        _currentLang = window.navigator.language || 'cn'
      }
      router.replace(replaceUrlRouter(_currentLang, to.fullPath))
    } else {
      // console.log("2.url中具备语言标识进入___else___:" + to.fullPath)
      if (to.fullPath.substr(0, 3) === '/en') {
        // console.log("3-1.url中具备语言标识进入___else_en___:" + to.fullPath)
        Cookie.set('jdcloud_sitelang', 'en', {
          expires: '50Y',
          domain: '.jdcloud.com'
        })
        i18n.locale = 'en'
        document
          .getElementsByClassName('jdc-rwd')[0]
          .setAttribute('class', 'jdc-rwd jdc-lang-en')
      } else if (to.fullPath.substr(0, 3) === '/cn') {
        // console.log("3-2.url中具备语言标识进入___else_cn___:" + to.fullPath)
        Cookie.set('jdcloud_sitelang', 'cn', {
          expires: '50Y',
          domain: '.jdcloud.com'
        })
        i18n.locale = 'cn'
        document
          .getElementsByClassName('jdc-rwd')[0]
          .setAttribute('class', 'jdc-rwd')
      } else {
        console.log('怎么都不会进入 测试____:' + to.fullPath)
        router.replace(Cookie.get('jdcloud_sitelang') + to.fullPath)
        // 不写入cookie是因为改变url后重新进入循环
      }
    }
  }
  next()
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  components: {
    App
  },
  template: '<App/>'
})



