import Vue from 'vue'
import Router from 'vue-router'
import mcee from '@/components/mcee'

// 语言切换配置
import {
  defaultLocale
} from '../config/i18n.js'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [{
    path: '',
    component: mcee
  },
  {
    path: '/en/',
    component: mcee
  },
  {
    path: '/cn/',
    component: mcee
  },
  {
    path: '*',
    name: 'mcee',
    component: mcee
  },
  {
    path: `/:${defaultLocale}`,
    component: {
      template: '<router-view />'
    }
  }
  ]
})



