          }
          img {
            width: 100%;
          }
          p {
            margin: 0;
            padding: 30px 20px;
            font-size: 14px;
            color: #333333;
          }
        }
      }
    }
  }
  .mcee-product {
    position: relative;
    .mcee-pet {
      left: 80px;
      bottom: 130px;
    }
    .mcee-product-content {
      max-width: 1200px;
      margin: 0 auto;
      text-align: center;
      .mcee-product-content-tablist {
        font-size: 18px;
        color: #000000;
        display: flex;
        justify-content: space-between;
        margin: 0 200px;
        .mcee-product-content-tab {
          width: 300px;
          padding-bottom: 4px;
          border-bottom: 2px solid #eeeeee;
          cursor: pointer;
          transition: all 0.3s;
          color: #333333;
        }
        .active {
          color: #4b9324;
          border-bottom: 5px solid #4b9324;
        }
      }
    }
    .mcee-product-content-wrap {
      height: 430px;
      text-align: center;
      position: relative;
      .mcee-product-content-inner-wrap {
        max-width: 1200px;
        position: absolute;
        top: 0px;
        left: 0;
        right: 0;
        margin: 0 auto;
        .mcee-product-content-list-wrap {
          transition: all 1s;
          opacity: 1;
          z-index: 2;
          position: absolute;
          width: 100%;
          height: auto;
          &.hidden-tab {
            opacity: 0;
            transition: all 1s;
            z-index: 1;
          }
          .mcee-product-content-list {
            display: flex;
            justify-content: space-between;
            padding: 50px 100px 0px;
            .mcee-product-content-item {
              .mcee-product-content-item-inner {
                height: 100px;
                position: relative;
                img {
                  margin: auto auto 0;
                  position: absolute;
                  left: 0;
                  right: 0;
                  bottom: 0;
                }
              }
              p {
                font-size: 16px;
                color: #333333;
                margin-top: 20px;
              }
            }
          }
        }
      }
    }

    .mcee-product-swiper {
      margin: 50px 24px 15px;
      .swiper-container {
        padding-bottom: 30px;
        .swiper-pagination-bullet {
          border-radius: 0px;
          width: 10px;
          height: 10px;
          background: #999999;
          display: inline-block;
          margin: 0 6px;
        }
        .swiper-pagination-bullet-active {
          background: #79bb55;
          transform: skewX(45deg);
          transform: rotate(45deg);
        }
        .swiper-button-prev {
          width: 47px;
          height: 54px;
          top: 35%;
          left: 0px;
          background-size: contain;
          background-image: url("../assets/images/mcee/learning_swiper_left.png");
        }
        .swiper-button-next {
          width: 47px;
          height: 54px;
          top: 35%;
          right: 0px;
          background-size: contain;
          background-image: url("../assets/images/mcee/learning_swiper_right.png");
        }
      }
      .product-swiper-item {
        width: 370px;
        img {
          width: 100%;
          height: 270px;
        }
        p {
          font-size: 14px;
          color: #252525;
          margin: 20px auto;
        }
      }
    }
    .mcee-product-bottom-wrap {
      .mcee-product-bottom {
        position: absolute;
        width: 100%;
        display: flex;
        height: 135px;
        background: #88b962;
        margin-top: 100px;
        top: 200px;
        img {
          position: absolute;
          bottom: 0px;
          left: 0;
          right: 0;
          margin: 0 auto;
        }
      }
    }
  }
  .mcee-example {
    background-image: url(../assets/images/mcee/example_bg.png);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    overflow: hidden;
    .mcee-tab-title {
      color: #ffffff;
    }
    .mcee-example-list-wrap {
      max-width: 1200px;
      margin: 0 auto;
      padding-bottom: 60px;
      .mcee-example-list {
        display: flex;
        justify-content: space-between;
        .mcee-example-item {
          text-align: center;
          position: relative;
          &::before {
            bottom: -4px;
            left: 1px;
            height: 5px;
          }
          &::after {
            bottom: -4px;
            right: 1px;
            height: 5px;
          }
          video {
            width: 360px;
          }
          .mcee-example-item-play-btn {
            position: absolute;
            left: 0;
            right: 0;
            margin: 0 auto;
            top: 50%;
            margin-top: -50px;
            cursor: pointer;
            height: 50px;
          }
          p {
            padding: 20px;
            font-size: 16px;
            color: #333333;
            background: #ffffff;
          }
        }
      }
    }
  }
  .mcee-try {
    background-image: url(../assets/images/mcee/try_bg.png);
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 60px 0px;
    .mcee-try-from-wrap {
      max-width: 1000px;
      margin: 0px auto;
      .mcee-try-from {
        background: #ffffff;
        position: relative;
        &::before {
          background-image: linear-gradient(-180deg, #e2e2e2 0%, #f1f1f1 100%);
          bottom: -12px;
          left: 4px;
          height: 12px;
        }
        &::after {
          background-image: linear-gradient(-180deg, #e2e2e2 0%, #f1f1f1 100%);
          bottom: -12px;
          right: 3px;
          height: 12px;
        }
        .mcee-tab-title {
          padding: 30px 0 30px;
        }
        .mcee-try-from-content {
          padding: 0px 50px 50px;
          display: flex;
          justify-content: space-between;
          .mcee-try-from-content-left {
            border: 1px solid #dbdbdb;
            color: #252525;
            width: 390px;
            height: 90%;
            .mcee-try-from-content-left-title {
              font-size: 14px;
              margin: 10px 20px;
              text-align: justify;
              font-weight: 700;
            }
            div {
              margin: 30px auto;
            }
            .mcee-try-from-content-left-subtitle {
              font-size: 12px;
              font-weight: 700;
              margin: 10px 30px;
              span {
                font-weight: 400;
              }
              .mcee-try-from-content-left-subtitle-two {
                margin-left: 85px;
              }
              .mcee-try-from-content-left-subtitle-xs {
                display: none;
              }
            }
          }
          .mcee-try-from-content-right {
            padding: 0 20px;
            .mcee-try-from-content-right-item {
              margin: 0px 15px 30px 15px;
              font-size: 14px;
              color: #252525;
              position: relative;
              label {
                margin: 5px;
                span {
                  color: #ff2626;
                  margin: 5px;
                }
              }
              .error_text {
                position: absolute;
                color: #e2231a;
                font-size: 12px;
                bottom: -22px;
                left: 80px;
              }
              input,
              select {
                height: 38px;
                width: 330px;
                padding-left: 20px;
                border: 1px solid #dbdbdb;
                border-radius: 5px;
              }
              select {
                width: 352px;
              }

              .select2-container--default .select2-selection--single {
                border: 1px solid #dbdbdb;
                border-radius: 5px;
                height: 38px;
                padding-left: 20px;
              }
              .select2-container
                .select2-selection--single
                .select2-selection__rendered {
                padding-left: 0px;
                padding-top: 3px;
              }
              .select2-container--default
                .select2-selection--single
                .select2-selection__arrow {
                top: 5px;
                b {
                  border-color: #8b67d7 transparent transparent transparent;
                  border-width: 12px 9px 0 9px;
                  left: 0%;
                }
              }
            }
            .mcee-try-btn {
              display: flex;
              position: relative;
              button {
                background-image: linear-gradient(
                  -177deg,
                  #8e6ada 0%,
                  #5b3baf 100%
                );
                font-size: 14px;
                color: #ffffff;
                text-align: center;
                padding: 5px;
                width: 100px;
                height: 36px;
                border: 1px solid;
                margin: 0 auto;
              }
            }
            .mcee-try-from-content-right-item-type {
              position: relative;
              .error_text {
                position: absolute;
                color: #e2231a;
                font-size: 12px;
                bottom: -22px;
                left: 100px;
              }
              label {
                .mcee-try-from-content-user-type-span {
                  color: #ff2626;
                  padding: 0 0px;
                  margin: 0 10px;
                }
                margin-right: 10px;
              }
              .mcee-user-type-label {
                padding: 0 10px;
              }
              .custome-radio {
                display: none;
              }
              .mcee-radio {
                display: inline-block;
                border: 1px solid #dbdbdb;
                border-radius: 5px;
                width: 16px;
                height: 16px;
                vertical-align: middle;
                margin-right: 5px;
                position: relative;
              }
              .mcee-radio:before {
                content: "";
                font-size: 0;
                width: 10px;
                height: 10px;
                background: #8b67d7;
                position: absolute;
                left: 50%;
                top: 50%;
                margin-left: -5px;
                margin-top: -5px;
                border-radius: 2px;
                display: none;
              }
              .custome-radio:checked ~ .mcee-radio:before {
                display: block;
              }
            }
          }
        }
      }
    }
  }
  // 成功提示框
  .success_feedback {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 10001;
    .success_feedback_mask {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.3);
    }
    .success_feedback_modal {
      width: 465px;
      height: 310px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -155px;
      margin-left: -232.5px;
      background-color: #fff;
      border-radius: 3px;
      overflow: hidden;
      .success_feedback_modal_title {
        font-size: 16px;
        height: 48px;
        line-height: 48px;
        padding-left: 20px;
        background-color: #f4f5f9;
        img {
          width: 15px;
          height: 15px;
          position: absolute;
          padding: 10px;
          right: 10px;
          top: 6px;
          cursor: pointer;
        }
      }
      .success_feedback_modal_content {
        text-align: center;
        padding-top: 52px;
        img {
          width: 46px;
          height: 46px;
        }
        p {
          color: #333;
          font-size: 14px;
          &:nth-child(2) {
            font-size: 18px;
            margin: 20px auto 10px;
          }
        }
      }
      .success_feedback_modal_footer {
        position: absolute;
        width: 100%;
        height: 48px;
        text-align: right;
        padding-right: 20px;
        background-color: #f4f5f9;
        bottom: 0;
        box-sizing: border-box;
        .success_feedback_modal_btn {
          text-align: center;
          width: 90px;
          line-height: 36px;
          height: 36px;
          border-radius: 3px;
          color: #fff;
          font-size: 16px;
          border: none;
          outline: none;
          margin-top: 6px;
        }
      }
    }
  }
  .mcee-bottom {
    background: #000000;
    .mcee-bottom-wrap-txt {
      font-size: 14px;
      color: #ffffff;
      text-align: center;
      padding: 20px 0 10px;
    }
    .mcee-bottom-wrap-logo {
      display: flex;
      img {
        height: 33px;
        margin: 10px auto 20px;
      }
    }
  }
}
.jdc-lang-en{
  .mcee-container{
    .mcee-banner-bottom .mcee-banner-content{
      h2{
        line-height: 1.1;
      }
      p{
        margin-top: 10px;
      }
    }
    .mcee-tablist{
      .one{
        width: 25%;
      }
    }
  }
}
@media (max-width: 1200px) {
  .mcee-container {
    .mcee-tab-title {
      font-size: 22px;
      padding: 30px 0;
    }
    .mcee-video-play-wrap {
      .inner_video_wrap {
        width: 100%;
        height: 40%;
        .video {
          height: 100%;
        }
      }
    }
    .mcee-banner-wrap {
      .mcee-banner-ico {
        height: 80px;
        margin: 0 0 0 20px;
        top: 20px;
      }
      .mcee-banner-bottom {
        height: 265px;
        background-image: url(../assets/images/mcee/mcee_xs/banner.png);
        .mcee-banner-content {
          top: 104px;
          text-align: left;
          margin-left: 20px;
          width: 88%;
          h2 {
            font-size: 30px;
          }
          p {
            font-size: 12px;
          }
        }
      }
      .mcee-tab-try-xs {
        display: flex;
        height: 25px;
        width: 120px;
        background: #34bb6d;
        color: #ffffff;
        position: absolute;
        bottom: 30px;
        margin-left: 20px;
        span {
          margin: 2px 0 0 5px;
        }
        img {
          height: 18px;
          margin: 3px 0 0 6px;
        }
      }
    }
    .mcee-tablist-wrap {
      display: none;
    }
    .mcee-myworld {
      .mcee-myworld-video {
        .mcee-myworld-video-inner {
          .video-scroll-bg {
            height: 100%;
          }
        }
        .mcee-myworld-video-wrap {
          margin: 0 10px;
          padding: 0 0 30px;
          .inner-video-wrap {
            width: 100%;
            img {
              width: 100%;
            }
            .video {
              position: relative;
              .myworld-video-play-btn {
                height: 40px;
                width: 35px;
                margin-top: -20px;
              }
            }

            p {
              font-size: 12px;
              line-height: 20px;
              padding: 10px;
            }
            &::before {
              bottom: -5px;
              left: 2px;
              height: 5px;
            }
            &::after {
              bottom: -5px;
              right: 2px;
              height: 5px;
            }
          }
        }
      }
      .mcee-myworld-list-wrap {
        padding: 15px 0px;
        .mcee-myworld-list {
          padding: 0px;
          .mcee-myworld-item {
            margin: 0px;
            flex: 1;
            img {
              height: 108px;
              width: 113px;
            }
            p {
              font-size: 12px;
              line-height: 18px;
              margin: 10px;
            }
          }
        }
      }
    }
    .mcee-future {
      .mcee-future-list {
        margin: 0 30px;
        margin-bottom: 10px;
        padding: 0px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        .mcee-future-item {
          width: 46%;
          margin-bottom: 20px;
          img {
            width: 100%;
            height: 140px;
          }
          .mcee-future-item-content {
            .mcee-future-item-inner {
              h2 {
                font-size: 16px;
                color: #333333;
                padding: 12px;
              }
              p {
                font-size: 12px;
                color: #000000;
                line-height: 18px;
                padding: 0 10px;
                margin-bottom: 10px;
              }
            }
          }
          &::before {
            bottom: -3px;
            left: 0px;
            height: 3px;
          }
          &::after {
            bottom: -3px;
            right: 0px;
            height: 3px;
          }
        }
      }
    }
    .mcee-learning {
      .mcee-learning-content {
        p {
          font-size: 12px;
          color: #333333;
          text-align: center;
          line-height: 18px;
          margin: 0 20px;
        }
      }
      .mcee-learning-content {
        .mcee-learning-swiper {
          display: none;
        }
        .mcee-learning-swiper-xs {
          display: flex;
          margin: 10px 20px;
          flex-wrap: wrap;
          justify-content: space-between;
          .mcee-learning-swiper-xs-item {
            width: 46%;
            margin: 10px auto;
            background: #ffffff;
            position: relative;
            &::before {
              bottom: -3px;
              left: 0px;
              height: 3px;
            }
            &::after {
              bottom: -3px;
              right: 0px;
              height: 3px;
            }
            img {
              width: 100%;
            }
            p {
              font-size: 12px;
              color: #252525;
              text-align: center;
              line-height: 14px;
              margin: 5px 15px;
            }
          }
        }
      }
    }
    .mcee-product {
      .mcee-product-content {
        display: none;
      }

      .mcee-product-content-wrap-xs {
        display: none;
      }
      .mcee-product-bottom-wrap {
        display: none;
      }

      .mcee-product-content-xs {
        display: block;
        padding: 0 20px;
        margin-bottom: 20px;
        .mcee-product-content-subtit-xs {
          height: 36px;
          background: #88b962;
          position: relative;
          text-align: center;
          margin-bottom: 20px;
          &::before {
            bottom: -3px;
            left: 1px;
            height: 3px;
            background: #5e8c3a;
          }
          &::after {
            bottom: -3px;
            right: 1px;
            height: 3px;
            background: #5e8c3a;
          }
          h3 {
            font-size: 16px;
            color: #ffffff;
            padding-top: 5px;
          }
          span {
            width: 0;
            height: 0;
            border-right: 14px solid transparent;
            border-left: 14px solid transparent;
            border-top: 14px solid #ffffff;
            right: 20px;
            position: absolute;
            top: 12px;
            transition: all 0.5s;
          }
          .active {
            border-bottom: 14px solid #ffffff;
            border-top: 0px;
            transition: all 0.5s;
            right: 20px;
            position: absolute;
            top: 12px;
          }
        }
        .mcee-product-content-teacher-list {
          display: flex;
          padding: 10px 30px;
          flex-wrap: wrap;
          justify-content: space-between;
          &.hidden {
            display: none;
          }
          .mcee-product-content-teacher-item {
            width: 40%;
            text-align: center;
            margin-bottom: 20px;
            position: relative;
            img {
              margin-bottom: 30px;
              width: 50%;
            }
            p {
              position: absolute;
              bottom: 0px;
              left: 0px;
              right: 0px;
              margin: 0 auto;
            }
          }
        }
        .mcee-product-content-class-list-xs {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          &.hidden {
            display: none;
          }
          .mcee-product-content-class-item-xs {
            width: 46%;
            margin-bottom: 10px;
            img {
              width: 100%;
            }
            p {
              text-align: center;
              font-size: 12px;
              line-height: 18px;
              margin: 5px;
            }
          }
        }
      }
    }
    .mcee-example {
      .mcee-example-list-wrap {
        padding-bottom: 10px;
        .mcee-example-list {
          flex-wrap: wrap;
          .mcee-example-item {
            margin: 0 auto;
            margin-bottom: 30px;
            p {
              font-size: 14px;
              text-align: center;
            }
            .mcee-example-item-play-btn {
            }
          }
        }
      }
    }
    .mcee-try {
      padding: 30px 0px;
      .mcee-try-from-wrap {
        margin: 10px;

        .mcee-try-from {
          &::before {
            bottom: -5px;
            left: 2px;
            height: 5px;
          }
          &::after {
            bottom: -5px;
            right: 2px;
            height: 5px;
          }
          .mcee-try-from-content {
            flex-wrap: wrap;
            padding: 0 10px;
            .mcee-try-from-content-left {
              .mcee-try-from-content-left-title {
                margin: 10px 15px;
              }
              .mcee-try-from-content-left-subtitle {
                .mcee-try-from-content-left-subtitle-pc {
                  display: none;
                }
                .mcee-try-from-content-left-subtitle-xs {
                  display: initial;
                }
              }
            }
            .mcee-try-from-content-right {
              width: 100%;
              padding: 0px;
              margin: 10px auto;

              .mcee-try-from-content-right-item {
                margin: 10px 0px;
                .error_text {
                  font-size: 12px;
                  bottom: -12px;
                  left: 5px;
                }
                input {
                  width: 95%;
                  padding-left: 10px;
                  margin: 7px 0px;
                }
                label {
                  span {
                    margin: 0px;
                  }
                }
              }
              .mcee-try-btn button {
                margin-top: 20px;
                width: 100%;
              }
            }
          }
        }
      }
    }
    .mcee-bottom {
      .mcee-bottom-wrap-txt {
        font-size: 10px;
      }
      .mcee-bottom-wrap-logo {
        img {
          height: 18px;
          margin: 0 auto 10px;
        }
      }
    }
    .success_feedback {
      .success_feedback_modal {
        width: 100%;
        width: 100%;
        margin-left: -190px;
      }
    }
  }
  .jdc-lang-en{
    .mcee-container{
      .mcee-product .mcee-product-content-xs .mcee-product-content-teacher-list .mcee-product-content-teacher-item{
        img{
          margin-bottom: 50px;
        }
        p{
          line-height: 1.1;
        }
      }
    }
  }
}
</style>



